/* Text Colors Classes */
.primary-text {
	/* Blue */
	color: $primary500;
}

.accent-text {
	/* Light Blue */
	color: $accent500;
}

.white-text {
	/* White */
	color: #fff;
}

.main-text {
	/* Dark Text */
	color: $main-text;
}

.secondary-text {
	/* Grey Text */
	color: $secondary-text;
}

/* Text Alignments */
.left-text {
	text-align: left;
}

.center-text {
	text-align: center;
}

.right-text {
	text-align: right;
}

/* Font Weights*/
.weight-300{
	font-weight: 300;
}

.weight-200{
	font-weight: 200;
}

.weight-100{
	font-weight: 100;
}

.main-view {
	/* main ui-router view */
	min-height: 100vh;
	position: relative;
	overflow: auto;
}

/* Stretch to full height */
.full-height-min {
	min-height: 100vh;
}
/* Max height 100% of viewport*/
.full-height-max {
	max-height: 100vh;
}

.full-width-min{
	min-width: 100%;
}

/* Toolbar Background */
md-toolbar.no-background-toolbar {
	background-color: white;
}

.site-logo {
	img{
		max-width: 60%;
		max-height: 100%;
	}
}

.personiv-logo {
	height:110px;
}

.hidden{
	opacity: 0;
}

#main{
	background: url('/assets/img/EASNZXBH4G.jpg');
	background-size: cover;
	transition: all 1s;
}

.main-content{
	background: rgba(#212121, 0.3);
}

/* Background colors */
md-content.dirty-white{
	background: #eeeeee;
}

md-content.blue-bg{
	background: $primary500;
}


.copyrights{
	width:100%;
	text-align: center;
	color: $secondary-text;
	padding: 15px 0px;
}

.left-border{
	border-left: 1px solid rgba(0,0,0,0.12);
}

.form{
	width: 300px;
}
